<i18n>
{
  "en": {
    "ModifyTheName": "Modify the name",
    "Save": "Save",
    "Support": "Support in English and Chinese",
    "characters": "2-20 letters, numbers and underscores"
  },
  "zh": {
    "ModifyTheName": "修改昵称",
    "Save": "保存",
    "Support": "支持中英文和数字",
    "characters": "2-20个字母、数字、下划线及中文组合"
  }
}
</i18n>

<template>
  <div class="rename">
    <head-top :title='$t("ModifyTheName")' :go-back="true">
      <button slot="setRight" @click="save"  :class="['save', {'active' : validate}]" :disabled="!validate">{{ $t('Save') }}</button>
    </head-top>
    <div class="content">
      <div class="name bomPixel">
        <input type="text" v-model="name" id="" :placeholder="placeholder" v-focus>
        <img src="../../assets/feedback_images/del.png" v-if="Version === 2" @click="name =''"/>
      </div>
      <div class="rename-box">
        <div class="tip">
          <span>{{ $t('characters') }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import headTop from '../../components/Header/index'
export default {
  name: 'rename',
  data () {
    return {
      name: '',
      placeholder: this.$t('Support')
    }
  },
  created () {
    if (!name) {
      this.name = this.userInfo.nickName
    }
  },
  computed: {
    ...mapGetters([
      'userInfo'
    ]),
    validate () {
      let reg
      if (this.$i18n.locale === 'zh') {
        reg = /^[a-zA-Z0-9_\u4e00-\u9fa5]{2,20}$/
      } else {
        reg = /^[a-zA-Z0-9_]{2,20}$/
      }
      return reg.test(this.name)
    }
  },
  components: {
    headTop
  },

  methods: {
    ...mapActions([
      'setUserInfo', 'loadingAlert'
    ]),

    goBackHander () {
      // console.log(123)
    },
    save () {
      // 修改昵称请求
      this.dataInteractionRequest({
        msgType: '0x5157',
        nickName: this.name
      })
      this.$router.back()
      this.loadingAlert(true)
      if (this.ISDEBUG) {
        let timer = setTimeout(() => {
          let obj = {
            'msgType': '0x515F',
            'result': true,
            'data': {
              'nickName': this.name
            }
          }
          window.dataInteractionResponse(JSON.stringify(obj))
          clearTimeout(timer)
        }, 1000)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.rename{
  .fs;
  background: #FFF;
  .save{
    .wh(110px, 100%);
    display: block;
    font-size: 30px;
    background: @statusBarColor;
    color: #cccccc;
    float: right;
  }
  .active{
    color: #282828;
  }
  .content{
    .fs-noTop;
    .name{
      position: relative;
      input{
        .wh-l(100%,78px);
        padding: 0 32px;
        font-size: 30px;
      }
      img{
        position: absolute;
        .wh(24px, 24px);
        top: 30px;
        right: 30px;
      }
    }
    .rename-box{
      .tip{
        text-align: left;
        padding: 15px 32px;
        color: #cccccc;
        span{
          display: block;
          font-size: 26px;
          .zoom-font(0.8);
        }
      }
    }
  }
}
</style>



